<template>
  <div class="container mt-5">
    <form class="row justify-content-center" @submit.prevent="signin" style="max-width: 900px; margin: auto;">
      <div class="col-md-6">
        <h1 class="h3 mb-3 font-weight-normal text-center">管理員登入</h1>
        <div class="mb-2">
          <label for="inputEmail" class="sr-only">帳號</label>
          <input
            type="email"
            id="inputEmail"
            class="form-control"
            placeholder="Email address"
            required
            autofocus
            v-model="user.username"
          />
        </div>
        <div class="mb-2">
          <label for="inputPassword" class="sr-only">密碼</label>
          <input
            type="password"
            id="inputPassword"
            class="form-control"
            placeholder="Password"
            required
            v-model="user.password"
          />
        </div>
    
        <div class="btn-wrap d-flex justify-content-center mt-4">
          <button class="btn-style" type="submit"><span>登入</span><i class="bi bi-arrow-right"></i></button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        user:{
          username: '',
          password: '',
        },
      }
    },
    methods: {
      signin(){
        const api = `${process.env.VUE_APP_API}/admin/signin`;
        this.$http.post(api,this.user)
        .then((res)=>{
          console.log(res);
          if(res.data.success){
            const { token, expired } = res.data;
            document.cookie = `hexToken=${token}; expires=${new Date(expired)};`;
            this.$router.push('/admin/products');
          }
        })
      },
    },
  }
</script>

<style lang="scss" scoped>
.form-control:focus{
  border: 1px solid #4f200d;
}
</style>